.feed {
    flex: 0.6;
    margin: 0 20px;
  }  
  
  .feed__inputContainer {
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
    box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.1); 
  }
  
  .feed__input {
    border: 1px solid lightgray;
    border-radius: 30px;
    display: flex;
    padding: 10px;
    color: gray;
    padding-left: 15px;
    align-items: center; 
  }
  
  .feed__input > form {
    display: flex;
    width: 100%;
  }
  
  .feed__input > form > input {
    border: none;
    flex: 1;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
    color: black;
  }
  
  .feed__input > form > button {
    display: none; 
  }
  
  .feed__inputOptions {
    display: flex;
    justify-content: space-evenly;
    padding-top: 10px; 
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .feed {
      margin: 0 10px; 
    }
  
    .feed__inputContainer,
    .feed__input {
      padding: 5px; 
    }
  
    .feed__input > form > input {
      font-size: 14px; 
    }
  }
  



