.header {
    position: sticky;
    top: 0;
    display: flex;
    background-color: white;
    justify-content: space-evenly;
    border-bottom: 1px solid lightgray; 
    padding: 10px 0; 
    width: 100%;
    z-index: 999;
  }
  
  .header__left {
    display: flex;
    align-items: center; 
  }
  
  .header__left > img {
    object-fit: contain;
    height: 40px;
    margin-right: 10px;
  }
  
  .header__search {
    padding: 10px;
    display: flex;
    align-items: center;
    border-radius: 5px;
    height: 22px;
    color: gray;
    background-color: #eef3f8;
    flex: 1; 
  }
  
  .header__search > input {
    outline: none;
    border: none;
    background: none;
    width: 100%; 
  }
  
  .header__right {
    display: flex;
    align-items: center; 
  }
  

  @media (max-width: 768px) {
    .header {
      justify-content: space-between; 
    }
  
    .header__left,
    .header__right {
      padding: 0 10px; 
    }
  }
  







