.headerOption {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
    color: gray;
    cursor: pointer;
    transition: color 0.3s; 
  }
  
  .headerOption__title {
    font-size: 12px;
    font-weight: 400;
    margin-top: 4px; 
  }
  
  .headerOption__icon {
    object-fit: contain;
    height: 25px; 
    width: 25px; 
  }
  
  .headerOption:hover,
  .headerOption:focus { 
    color: black;
  }

  


