.app {
  background-color: #f3f2ef;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh; 
  padding: 0 20px; 
}

.app__body {
  display: flex;
  margin-top: 35px;
  max-width: 1200px;
  width: 100%; 
  justify-content: center; 
  gap: 20px; 
}

/* Responsive styles for smaller screens */
@media (max-width: 768px) {
  .app__body {
    flex-direction: column; 
    align-items: center; 
  }
}
